	import http from '@/components/utils/http.js';
export default {
	data() {
		return {
			title: 'Hello',
			bigIdx: 2,
			vips: [],
			tasks: [
				{
					icon: '/static/icon_profile.png',
					name: '完善个人信息',
					desc: '完善信息获得7积分奖励'
				},
				{
					icon: '/static/icon_friend.png',
					name: '邀请好友参加',
					desc: '邀请好友获得7积分奖励'
				}
			],
			welfare: [
				{
					icon: 'icon-fenxiang1',
					name: '分享邀请'
				},
				{
					icon: 'icon-zhekou1',
					name: '享受折扣'
				},
				{
					icon: 'icon-cu',
					name: '促销活动'
				},
				{
					icon: 'icon-shi',
					name: '0元试用'
				}
			]
		};
	},
	onLoad() {
		
		let opts = {
			url: '/base/getusertypelist/',
			method: 'post'
		};
		let param = {};
		http.httpTokenRequest(opts, param).then(
			res => {
				if (res.data['code'] == 0) {
					this.bigIdx = res.data['bigIdx']-1;
					this.vips= res.data['vips'];
				}
			},
			error => {
				console.log(error);
			}
		);
		
	},
	methods: {
		swiperChange(e) {
			this.bigIdx = e.detail.current;
		},
		navTo(url) {
			uni.navigateTo({
				url
			});
		},
	}
};
